export default function Legend({ selectedFuel }) {
  return (
    selectedFuel ? (
      <>
        <div>
          <p> : Prix bas</p>
        </div>
        <div>
          <p> : Prix intermdiaire</p>
        </div>
        <div>
          <p> : Prix levs</p>
        </div>
        <div>
          <p> : Pas de prix</p>
        </div>
      </>
    ) : (
      <>
        <div> : Pas de prix</div>
      </>)
  )
}